export function validateSignup(values) {
    let errors = {};

    if (!values?.fullName?.trim()) {
        errors.fullName = "Ваше имя пользователя";
    }
    if (!values?.phoneNumber) {
        errors.phoneNumber = "Ваше имя пользователя";
    }
    if (!values?.email?.trim()) {
        errors.email = "Ваше имя пользователя";
    }
    if (!values?.address1?.trim()) {
        errors.address1 = "Ваше имя пользователя";
    }
    if (!values?.address2?.trim()) {
        errors.address2 = "Ваше имя пользователя";
    }
    if (!values?.zipcode) {
        errors.zipcode = "Ваш пароль";
    }
    if (!values?.city?.trim()) {
        errors.city = "Ваше имя";
    }

    return errors;
}
