import React, { useEffect, useState } from "react";
import emailjs from "@emailjs/browser";
import { useNavigate } from "react-router";
import { validateSignup } from "../components/validate";
import isEmpty from "../components/isEmpty";

function Home() {
    const [formInputs, setFormInputs] = useState({
        fullName: "",
        phoneNumber: "",
        email: "",
        address1: "",
        address2: "",
        zipcode: "",
        city: "",
    });
    const [formErrors, setFormErrors] = useState(null);
    const navigate = useNavigate();

    const handleChange = (e) => {
        const value = e.target.value;
        const name = e.target.name;
        setFormInputs({ ...formInputs, [name]: value });
    };

    useEffect(() => {
        setFormErrors(validateSignup(formInputs));
    }, [formInputs]);

    const onSubmit = (e) => {
        e.preventDefault();
        console.log("EMAIL SEND");
        const message = `
        Full Name: ${formInputs.fullName}
        Phone number: ${formInputs.phoneNumber}
        Email: ${formInputs.email}
        Address1: ${formInputs.address1}
        Address2: ${formInputs.address2}
        Zip code: ${formInputs.zipcode}
        City: ${formInputs.city}
        `;

        emailjs
            .send(
                "service_fju1svr",
                "template_85f6i4d",
                { fullName: formInputs.fullName, message: message },
                "hhBU5Eb9llm0Pv9nX"
            )
            .then(
                (result) => {
                    console.log(result.text);
                    navigate("/success");
                },
                (error) => {
                    console.log("ERROR", error.text);
                    e.reset();
                }
            );
    };

    return (
        <div style={{ backgroundColor: "#F5F9FA", minHeight: "100vh" }}>
            <div className="d-flex justify-content-center align-items-center">
                <img
                    src="/images/logo.png"
                    style={{ height: "100px" }}
                    alt=""
                />
            </div>
            <div className="fs-16 text-center fw-bold mb-4">
                To begin add your second business partner details
            </div>
            <div className="row justify-content-center align-items-center mx-0">
                <div className="col-11 col-md-4 bg-white rounded-1 shadow px-3 py-3 mb-4">
                    <div className="fw-light text-center">
                        Personal information
                    </div>
                    <form className="fs-14 py-3" onSubmit={onSubmit}>
                        <div class="mb-3">
                            <label for="fullNameInput" class="form-label">
                                Full name
                            </label>
                            <input
                                type="text"
                                class="form-control fs-14 border rounded-0"
                                style={{ boxShadow: "none" }}
                                id="fullNameInput"
                                value={formInputs?.fullName}
                                name="fullName"
                                onChange={(e) => handleChange(e)}
                                aria-describedby="emailHelp"
                            />
                            <div id="emailHelp" class="form-text">
                                We'll never share your email with anyone else.
                            </div>
                        </div>
                        <div class="mb-3">
                            <label for="phoneNumber" class="form-label">
                                Phone Number
                            </label>
                            <input
                                type="number"
                                style={{ boxShadow: "none" }}
                                value={formInputs?.phoneNumber}
                                name="phoneNumber"
                                onChange={(e) => handleChange(e)}
                                class="form-control border fs-14 rounded-0"
                                id="phoneNumber"
                            />
                        </div>
                        <div class="mb-3">
                            <label for="email" class="form-label">
                                Email
                            </label>
                            <input
                                type="email"
                                style={{ boxShadow: "none" }}
                                class="form-control border fs-14 rounded-0"
                                id="email"
                                value={formInputs?.email}
                                name="email"
                                onChange={(e) => handleChange(e)}
                            />
                        </div>
                        <div class="horizontal-line-container">
                            <div class="horizontal-line"></div>
                            <div class="or-text">and</div>
                        </div>

                        <div class="mb-3">
                            <label for="address1" class="form-label">
                                Address line 1
                            </label>
                            <input
                                type="text"
                                class="form-control fs-14 border rounded-0"
                                style={{ boxShadow: "none" }}
                                id="address1"
                                value={formInputs?.address1}
                                name="address1"
                                onChange={(e) => handleChange(e)}
                            />
                        </div>
                        <div class="mb-3">
                            <label for="address2" class="form-label">
                                Address line 2 (apartment number)
                            </label>
                            <input
                                type="text"
                                class="form-control fs-14 border rounded-0"
                                style={{ boxShadow: "none" }}
                                id="address2"
                                value={formInputs?.address2}
                                name="address2"
                                onChange={(e) => handleChange(e)}
                            />
                        </div>
                        <div class="mb-3">
                            <label for="zipcode" class="form-label">
                                Zip code
                            </label>
                            <input
                                type="number"
                                class="form-control fs-14 border rounded-0"
                                style={{ boxShadow: "none" }}
                                id="zipcode"
                                value={formInputs?.zipcode}
                                name="zipcode"
                                onChange={(e) => handleChange(e)}
                            />
                        </div>
                        <div class="mb-3">
                            <label for="city" class="form-label">
                                City
                            </label>
                            <input
                                type="text"
                                class="form-control fs-14 border rounded-0"
                                style={{ boxShadow: "none" }}
                                id="city"
                                value={formInputs?.city}
                                name="city"
                                onChange={(e) => handleChange(e)}
                            />
                        </div>
                        <div>
                            <div>Country</div>
                            <select
                                class="form-select fs-14 rounded-0 mt-2"
                                disabled
                                aria-label="Default select example"
                            >
                                <option selected>Turkey</option>
                            </select>
                        </div>

                        <button
                            className="orangebtn py-2 fw-bold mt-4"
                            type="submit"
                            disabled={!isEmpty(formErrors)}
                        >
                            Submit
                        </button>
                    </form>
                </div>
            </div>
        </div>
    );
}

export default Home;
